.fade-in-img {
    opacity: 0;
    transform: scale(.9, 1);
    transform-origin: left;
    transition: opacity .7s ease-out, transform .7s ease-out;
}

.fade-in-img.is-inview {
    opacity: 1;
    transform: scale(1, 1);
}

.img-last.fade-in-img,
.img-1-big.fade-in-img {
    transform-origin: center !important;  /* Transform origin to center for these classes */
}